import React from "react";

export interface ButtonProps {
  // `export`를 추가하여 타입 공개
  children: React.ReactNode;
  onClick?: () => void;
  type?: "button" | "submit" | "reset";
}

export const Button: React.FC<ButtonProps> = ({
  children,
  onClick,
  type = "button",
}) => {
  return (
    <button
      onClick={onClick}
      type={type}
      style={{
        padding: "10px 20px",
        border: "none",
        background: "#007bff",
        color: "white",
        borderRadius: "4px",
      }}
    >
      {children}
    </button>
  );
};
